import React from 'react';
import MainLayout from '../../components/MainLayout'
import CtaButton from '../../components/CtaButton'
import Block from '../../components/Block'
import styled from 'styled-components';
import Helmet from "react-helmet"

const QuestionBox = styled.div`
 border: 1px solid grey;
 margin: 5px 0px;
`
const Container = styled.div`
padding: 4px;
`
const val = [{name: "Array"}, {name: "Object"}, {name: "Function"}]
class Quiz extends React.Component {
    state = {
        res: null,
        checked: "Two"
    }
    render() {
        
        return(
            <QuestionBox>
        <Helmet title={"Reduxjs simple quiz"} >
        <meta name="description" content="A simple quiz to test your understanding on reduxjs terminologies like
        reducer, state and store" />
        </Helmet>
                <Container>
                <p> {this.props.ques} </p>
                <div>
                    {val.map((i) => 
                <label className="container">{i.name}
                        <input type="radio" checked={i.name == this.state.checked} name="radio" onChange={() => {
                            const res = this.props.ans === i.name;
                            this.setState({checked: i.name, res})
                        }}/>
                        <span className="checkmark"></span>
                </label>
                    )}
                    {this.state.res == null ? null : this.state.res ? <p> You're right </p> : <p> Please try again </p>  }
                </div>
                </Container>
            </QuestionBox>
        );
    }
}
export default (props) =>
    <MainLayout>
        <h3>  A quick quiz </h3>

        <Quiz key="1" ques={`1. reducer a.k.a instructions is a`} ans="Function"/>
        <Quiz key="2" ques={`2. state a.k.a account Information is an`} ans="Object"/>
        <Quiz key="3" ques={`3. store a.k.a the Account is an`} ans="Object"/>
        <CtaButton to="/redux-intro/react-redux">So far good, next</CtaButton>
    </MainLayout>


